import React, { useState, useEffect } from "react";
import { InputGroup, ListGroup, FormControl, Container, Row, Col, Card, Accordion, Button, ButtonGroup, ToggleButton } from "react-bootstrap";
import { LangModule, LangModuleMask } from "./LangModule";
import "./App.scss";

// Default source for language module data
const DEFAULT_SOURCE = "https://raw.githubusercontent.com/nilsso/nilsso.github.io/code/static/apps/lang_modules/japanese_modules.json";

// Helper functions
const mask = (length: number, fill: boolean) => Array(length).fill(fill);
const toggle_mask = (mask: Array<boolean>, i: number, value: boolean) => {
    let res = [...mask];
    res[i] = value;
    return res;
};

// Fetch ("GET" request) file from URL and parse a JSON
async function requestJSON(url: string) {
    var module_data;
    await fetch(url)
        .then(response => response.json())
        .then(data => module_data = data)
        .catch(e => alert("Invalid source URL\n" + e));
    if (module_data) {
        return module_data;
    } else {
        return [];
    }
}

function App() {
    // Module source
    const [source, setSource] = useState(DEFAULT_SOURCE);
    // Modules
    const [modules, setModules] = useState(Array<LangModule>(0));
    // Joined module
    const [joined, setJoined] = useState(LangModule.empty());
    // Module masks
    const [enabledMask, setEnabledMask] = useState(mask(0, false));
    const [fragmentMask, setFragmentMask] = useState(mask(0, true));
    const [vocabularyMask, setVocabularyMask] = useState(mask(0, true));

    // If module source changed build new modules
    useEffect(() => {
        requestJSON(source).then(data => setModules(data.map(json => LangModule.from_json(json))));
    }, [source]);

    // If modules changed update the module masks
    useEffect(() => {
        setEnabledMask(mask(modules.length, false));
        setFragmentMask(mask(modules.length, true));
        setVocabularyMask(mask(modules.length, true));
    }, [modules]);

    // If module masks changed update the joined module
    useEffect(() => {
        if (modules.length > 0) {
            setJoined(LangModule.join(modules, new LangModuleMask(enabledMask, fragmentMask, vocabularyMask)));
        }
    }, [modules, enabledMask, fragmentMask, vocabularyMask]);

    return (
        <Container>
            <Row>
                <GeneratorComponent joined={joined}/>
                <ModulesComponent
                    modules={modules}

                    enabledMask={enabledMask}
                    setEnabledMask={setEnabledMask}

                    fragmentMask={fragmentMask}
                    setFragmentMask={setFragmentMask}

                    vocabularyMask={vocabularyMask}
                    setVocabularyMask={setVocabularyMask}
                />
            </Row>
            <Source source={source} setSource={setSource}/>
        </Container>
    );
}

function Source(props: any) {
    const [source, setSource] = [props.source, props.setSource];

    const [value, setValue] = useState(source);

    return (
        <Row>
            <Col className="component">
                <InputGroup>
                    <InputGroup.Prepend>
                        <InputGroup.Text>
                            Module Source
                        </InputGroup.Text>
                    </InputGroup.Prepend>
                    <FormControl
                        defaultValue={value}
                        onChange={e => setValue(e.target.value)}
                    />
                    <InputGroup.Append>
                        <Button
                            variant="outline-secondary"
                            onClick={() => setSource(value)}
                        >
                            Load
                        </Button>
                    </InputGroup.Append>
                </InputGroup>
            </Col>
        </Row>
    );
}

function GeneratorComponent(props: any) {
    const joined = props.joined;

    const [generated, setGenerated] = useState("...");
    const [meaning, setMeaning] = useState("");
    const [hidden, setHidden] = useState("...");
    const [swap, setSwap] = useState(false);

    return (
        <Col className="component">
            <Card>
                <Card.Header>Generator</Card.Header>
                <ListGroup variant="flush">
                    <ListGroup.Item>
                        <ButtonGroup>
                            <Button
                                onClick={() => {
                                    let [generated, meaning] = joined.random();
                                    if (generated) {
                                        [generated, meaning] = swap ? [meaning, generated] : [generated, meaning];
                                        setHidden("...");
                                        setGenerated(generated);
                                        setMeaning(meaning);
                                    }
                                }
                                }
                            >
                                Generate
                            </Button>
                            <Button
                                variant="success"
                                onClick={() => setHidden(meaning)}
                            >
                                Reveal
                            </Button>
                            <Button
                                active={swap}
                                variant="outline-secondary"
                                onClick={() => setSwap(!swap)}
                            >
                                Swap
                            </Button>
                        </ButtonGroup>
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <p>{generated}</p>
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <p>{hidden}</p>
                    </ListGroup.Item>
                </ListGroup>
            </Card>
        </Col>
    );
}

function ModulesComponent(props: any) {
    // Modules state
    const modules = props.modules;
    // Mask states
    const [enabledMask, setEnabledMask] = [props.enabledMask, props.setEnabledMask];
    const [fragmentMask, setFragmentMask] = [props.fragmentMask, props.setFragmentMask];
    const [vocabularyMask, setVocabularyMask] = [props.vocabularyMask, props.setVocabularyMask];

    // Helper to toggle all modules
    const toggleAll = function(checked: boolean) {
        setEnabledMask(mask(modules.length, checked));
    }

    let module_components = modules.map((lang_module: LangModule, i: number) => {
        return <LangModuleComponent
            key={i}
            lang_module={lang_module}

            enabledFlag={enabledMask[i]}
            fragmentFlag={fragmentMask[i]}
            vocabularyFlag={vocabularyMask[i]}

            setEnabledFlag={(checked: boolean) => setEnabledMask(toggle_mask(enabledMask, i, checked))}
            setFragmentFlag={(checked: boolean) => setFragmentMask(toggle_mask(fragmentMask, i, checked))}
            setVocabularyFlag={(checked: boolean) => setVocabularyMask(toggle_mask(vocabularyMask, i, checked))}
        />;
    });

    return (
        <Col className="component">
            <Card>
                <Card.Header>Modules</Card.Header>
                <ListGroup variant="flush">
                    <ListGroup.Item>
                        <ButtonGroup>
                            <Button
                                variant="primary"
                                onClick={() => toggleAll(true)}
                            >
                                All
                            </Button>
                            <Button
                                variant="danger"
                                onClick={() => toggleAll(false)}
                            >
                                None
                            </Button>
                        </ButtonGroup>
                    </ListGroup.Item>
                    <ListGroup.Item>
                        <div id="module-list">{module_components}</div>
                    </ListGroup.Item>
                </ListGroup>
            </Card>
        </Col>
    );
}

function LangModuleComponent(props: any) {
    // Modules state
    const lang_module = props.lang_module;
    // Mask states
    const [enabledFlag, setEnabledFlag] = [props.enabledFlag, props.setEnabledFlag];
    const [fragmentFlag, setFragmentFlag] = [props.fragmentFlag, props.setFragmentFlag];
    const [vocabularyFlag, setVocabularyFlag] = [props.vocabularyFlag, props.setVocabularyFlag];

    const has_fragments = !!fragmentFlag;
    const has_vocabulary = !!vocabularyFlag;

    return(
        <Accordion className="module">
            <Card>
                <Card.Body id="module-control">
                    <ButtonGroup
                        toggle className="module-controls"
                    >
                        <ToggleButton
                            type="checkbox"
                            variant="outline-primary"
                            size="sm"
                            value={0}
                            checked={enabledFlag}
                            onChange={(e) => setEnabledFlag(e.currentTarget.checked)}
                        >
                            {lang_module.name}
                        </ToggleButton>
                        { has_fragments &&
                            <ToggleButton
                                type="checkbox"
                                variant="outline-secondary"
                                size="sm"
                                value={1}
                                checked={fragmentFlag}
                                disabled={!enabledFlag}
                                onChange={(e) => setFragmentFlag(e.currentTarget.checked)}
                            >
                                Fragments
                            </ToggleButton>
                        }
                        { has_vocabulary &&
                            <ToggleButton
                                type="checkbox"
                                variant="outline-secondary"
                                size="sm"
                                value={2}
                                checked={vocabularyFlag}
                                disabled={!enabledFlag}
                                onChange={(e) => setVocabularyFlag(e.currentTarget.checked)}
                            >
                                Vocabulary
                            </ToggleButton>
                        }
                    </ButtonGroup>
                </Card.Body>
            </Card>
            <Card>
                { has_fragments && <FragmentListComponent lang_module={lang_module} /> }
                { has_vocabulary && <VocabularyListComponent lang_module={lang_module} /> }
            </Card>
        </Accordion>
    );
}

function FragmentListComponent(props: any) {
    const lang_module = props.lang_module;

    const rows = lang_module.fragments.map((s, i) => {
        return (
            <Row key={i} className="module-content-item">
                <Col>{s.fragment_template}</Col>
                <Col>{s.meaning_template}</Col>
            </Row>
        );
    });

    return (
        <React.Fragment>
            <Accordion.Toggle
                as={Card.Header}
                className="small module-contents-header"
                variant="link"
                eventKey="0"
            >
                View Fragments
            </Accordion.Toggle>
            <Accordion.Collapse eventKey="0">
                <Card.Body>
                    <Card className="module-content">
                        <Container fluid>
                            {rows}
                        </Container>
                    </Card>
                </Card.Body>
            </Accordion.Collapse>
        </React.Fragment>
    );
}

function VocabularyListComponent(props: any) {
    const lang_module = props.lang_module;

    const token_rows = function(choices) {
        return choices.map(([vocab, meaning], i) => {
            return (
                <Row key={i} className="module-content-item">
                    <Col>{vocab}</Col>
                    <Col>{meaning}</Col>
                </Row>
            );
        });
    }

    const vocabulary_rows = Object.entries(lang_module.vocabulary).map(([token, choices], i) => {
        return (
            <React.Fragment key={i}>
                <ListGroup.Item variant="secondary">{token}</ListGroup.Item>
                <ListGroup.Item className="vocabulary">
                    <Container>
                        {token_rows(choices)}
                    </Container>
                </ListGroup.Item>
            </React.Fragment>
        );
    });

    return (
        <React.Fragment>
            <Accordion.Toggle
                as={Card.Header}
                className="small module-contents-header"
                variant="link"
                eventKey="1"
            >
                View Vocabulary
            </Accordion.Toggle>
            <Accordion.Collapse eventKey="1">
                <Card.Body>
                    <Card className="module-content">
                        <ListGroup variant="flush">
                            {vocabulary_rows}
                        </ListGroup>
                    </Card>
                </Card.Body>
            </Accordion.Collapse>
        </React.Fragment>
    );
}

export default App;
